@import '../../../base/mixins';
@import '../../../base/style.scss';
@import '../../../base/variables';
@import '../../../base/defaultstyle';

* {
    font-family: var(--unnamed-font-family-source-serif-pro);
}

.cart {
    .quantity-section {
        .count-wrapper {
            input {
                width: 87px;
            }
        }
    }

    .headline {
        font: normal normal 600 36px/45px Source Serif Pro;
        margin: 10px 0;
    }

    .band {
        width: 102px;
        height: 8px;
        background: var(--primary-brand) 0% 0% no-repeat padding-box;
        margin: auto;
    }

    .card-img {
        width: 166px;
        height: 199px;

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    .product-card-main {
        margin-top: 20px;
    }

    .description {
        h5 {
            font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
            margin-bottom: 10px;
        }

        p {
            font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
            margin-bottom: 10px;
        }
    }

    .icon-img {
        margin-right: 10px;
    }

    .summary {
        border: 1px solid #D1D3D6;
        padding: 25px;
        margin-bottom: 30px;

        h5 {
            font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-20)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-muli);
            color: var(--unnamed-color-172026);
            font: normal normal 600 20px/30px Mulish;
            margin-bottom: 10px;
        }

        .pricing-details {
            div {
                display: flex;
                justify-content: space-between;
                margin-bottom: 10px;

                p {
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
                    font: normal normal normal 16px/24px Mulish;
                }

            }

            .btn-div {
                justify-content: center;

                button {
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-14)/16px var(--unnamed-font-family-muli);
                    font: normal normal bold 14px/16px Mulish;
                }
            }

            .pricing-total {
                p {
                    color: var(--unnamed-color-172026);
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
                    font: normal normal bold 16px/24px Mulish;
                }

            }
        }
    }
}

@include desktop {
    .product-card-main {
        padding-right: 20px;
    }
}